

export default {
    init: function() {
        let collectionWrapper = document.getElementById("collection-items-list")
        let collectionPagesWrapper = document.getElementById("collection-items-paginator")
        if(!collectionWrapper) return

        const itemsPerPage = 20
        let currentPage = 0
        const items = Array.from(collectionWrapper.getElementsByTagName('div'))
        const currPage = []
        const pages = []
        let count = 1

        for(let i = 0; i < items.length; i++) {
            if(count == 20) {
                count = 1
                pages.push(currPage)
                currPage = []
            }
            currPage.push(items[i])
            count += 1
        }
        
        function showPage(page) {
            /*const startIndex = page * itemsPerPage
            const endIndex = startIndex + itemsPerPage
            items.forEach((item, index) => {
              if (index < startIndex || index >= endIndex) {
                item.style.display = "none"
              } else {
                item.style.display = "block"
              }
            })*/
            collectionWrapper.innerHTML = '';
            collectionWrapper.appendChild( pages[page] );
            updateActiveButtonStates()
        }

        function createPageButtons() {
            const totalPages =pages.length
            const paginationContainer = document.createElement('div')
            const paginationDiv = document.body.appendChild(paginationContainer)
            paginationContainer.classList.add('pagination')
          
            // Add page buttons
            for (let i = 0; i < totalPages; i++) {
                const pageButton = document.createElement('button')
                pageButton.textContent = i + 1
                pageButton.addEventListener('click', () => {
                    currentPage = i
                    showPage(currentPage)
                    updateActiveButtonStates()
                })
        
                collectionPagesWrapper.appendChild(paginationContainer)
                paginationDiv.appendChild(pageButton)
            }
        }
          
        function updateActiveButtonStates() {
            const pageButtons = document.querySelectorAll('.pagination button')
            pageButtons.forEach((button, index) => {
              if (index === currentPage) {
                button.classList.add('active')
              } else {
                button.classList.remove('active')
              }
            })
        }
          
        createPageButtons() // Call this function to create the page buttons initially
        showPage(currentPage)
    }
}